@charset "UTF-8";
/* -------------------------------------------------------------
   IMPORTACIONES DE SASS (deben ir SIEMPRE primero)
------------------------------------------------------------- */
@import "https://unpkg.com/aos@2.3.1/dist/aos.css";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

p {
  margin-bottom: 1rem;
  font-size: 1rem;
  line-height: 1.6;
}

header {
  @apply bg-white shadow-md sticky top-0 z-50;
}
header .header__logo {
  @apply text-2xl font-bold text-primario;
}
header nav a {
  @apply text-gray-700 hover:text-primario font-medium transition;
}
header .header__contacto {
  @apply bg-primario text-white px-4 py-2 rounded-md hover:bg-indigo-700 transition font-medium;
}

/* ==========================================================
   HERO SECTION
   ========================================================== */
.hero {
  @apply bg-indigo-50;
}
.hero .hero__container {
  @apply container mx-auto flex flex-col-reverse md:flex-row items-center py-20 px-4 md:px-0;
}
.hero .hero__text {
  @apply md:w-1/2 text-center md:text-left mb-10 md:mb-0;
}
.hero .hero__text h1 {
  @apply text-4xl md:text-5xl font-bold text-gray-900 mb-6;
}
.hero .hero__text p {
  @apply text-gray-700 text-lg mb-6;
}
.hero .hero__text a {
  @apply bg-primario text-white px-6 py-3 rounded-md hover:bg-indigo-700 transition font-medium;
}
.hero .hero__image {
  @apply md:w-1/2 flex justify-center md:justify-end;
}
.hero .hero__image img {
  @apply rounded-lg shadow-xl object-contain;
}

/* ==========================================================
   FOOTER
   ========================================================== */
footer {
  @apply bg-gray-900 text-gray-300 py-10 mt-20;
}
footer .footer__container {
  @apply container mx-auto grid grid-cols-1 md:grid-cols-3 gap-8 px-4;
}
footer .footer__col h3 {
  @apply text-white font-semibold text-lg mb-4;
}
footer .footer__col a {
  @apply block text-gray-400 hover:text-white transition-colors mb-2;
}
footer .footer__col p {
  @apply text-gray-400 leading-relaxed;
}
footer .footer__bottom {
  @apply border-t border-gray-700 mt-10 pt-6 text-center text-sm text-gray-500;
}

/* -------------------------------------------------------------
   DIRECTIVAS DE TAILWIND (después de los @use)
------------------------------------------------------------- */
@tailwind base;
@tailwind components;
@tailwind utilities;
/* -------------------------------------------------------------
   ESTILOS PERSONALIZADOS
------------------------------------------------------------- */
body {
  @apply font-sans text-gray-800 bg-gray-50;
}

/* -------------------------------------------------------------
   SECCIONES (usa clases directamente, no vuelvas a usar @use)
------------------------------------------------------------- */
.header {
  @apply bg-white shadow-md;
}

.hero {
  @apply text-center py-16 bg-indigo-600 text-white;
}

.footer {
  @apply bg-gray-900 text-white text-center py-8;
}

@layer utilities {
  /* --- Animaciones previas --- */
  @keyframes whatsapp-glow {
    0%, 100% {
      transform: scale(1);
      box-shadow: 0 0 10px rgba(16, 185, 129, 0.5);
    }
    50% {
      transform: scale(1.08);
      box-shadow: 0 0 25px rgba(16, 185, 129, 0.9);
    }
  }
  @keyframes whatsapp-float {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-6px);
    }
  }
  /* --- Botón --- */
  .whatsapp-float {
    position: fixed;
    bottom: 25px;
    right: 25px;
    z-index: 50;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #25D366;
    border-radius: 50%;
    padding: 0.8rem;
    animation: whatsapp-entry 0.8s ease-out, whatsapp-float 3s ease-in-out infinite 0.8s, whatsapp-glow 2s ease-in-out infinite 0.8s;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  .whatsapp-float:hover {
    animation: whatsapp-shake 0.3s ease-in-out;
    transform: scale(1.1);
    box-shadow: 0 0 30px rgba(16, 185, 129, 0.9);
  }
  /* --- Tooltip --- */
  .whatsapp-tooltip {
    position: absolute;
    right: 75px;
    background: #1f2937; /* gris oscuro elegante */
    color: white;
    padding: 0.5rem 0.8rem;
    border-radius: 0.5rem;
    font-size: 0.9rem;
    white-space: nowrap;
    opacity: 0;
    transform: translateX(10px);
    pointer-events: none;
    transition: all 0.3s ease;
  }
  .whatsapp-float:hover .whatsapp-tooltip {
    opacity: 1;
    transform: translateX(0);
  }
  /* Triángulo del tooltip */
  .whatsapp-tooltip::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -6px;
    transform: translateY(-50%);
    border-left: 6px solid #1f2937;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
  }
}